define('dummy/router', ['exports', 'ember', 'dummy/config/environment', 'dummy/mixins/google-page-view'], function (exports, Ember, config, googlePageView) {

  'use strict';

  var Router = Ember['default'].Router.extend(googlePageView['default'], {
    location: config['default'].locationType
  });

  exports['default'] = Router.map(function () {
    this.route('buttons');
    this.route('content');
    this.route('divider');
    this.route('card');
    this.route('input');
    this.route('list');
    this.route('toolbar');
    this.route('checkbox');
    this.route('slider');
    this.route('progress-circular');
    this.route('progress-linear');
    this.route('icon');
    this.route('radio-button');
    this.route('tabs');
    this.route('typography');
    this.route('tooltip');
    this.route('toast');
  });

});